import React from "react";
import { Link } from "react-router-dom";
const CardItem = (props) => {
  const { addToCart, product, showProductDetail } = props;

  return (
    <>
      <div className="col">
        <div className="card  h-100 mb-4 product-wap rounded-0">
          <div className="card rounded-0">
            <img
              className="card-img rounded-0 img-fluid"
              src={product.proAvatar}
              alt="product"
            />
            <div className="card-img-overlay rounded-0 product-overlay d-flex align-items-center justify-content-center">
              <ul className="list-unstyled">
                <li>
                  <button
                    className="btn btn-success text-white mt-2"
                    onClick={showProductDetail}
                  >
                    <i className="far fa-eye" />
                  </button>
                </li>
                <li>
                  <button
                    className="btn btn-success text-white mt-2"
                    onClick={() => addToCart(product)}
                  >
                    <i className="fas fa-cart-plus" />
                  </button>
                </li>
              </ul>
            </div>
          </div>
          <div className="card-body">
            <Link
              to="/productdetail"
              className="h3 text-decoration-none text-dark"
            >
              {product.proName}
            </Link>
            <h3 className="fw-bold text-danger text-left pt-3 ">
              {product.proPrice}
              <sup>
                <i className="fa-solid fa-dong-sign"></i>
              </sup>
            </h3>
          </div>
        </div>
      </div>
    </>
  );
};

export default CardItem;
