import React, { useState, useEffect } from "react";
import { useDispatch } from "react-redux";
import { useNavigate, useParams } from "react-router-dom";
import { toast } from "react-toastify";
// import { increase, decrease } from "../store/reducers/cart";
import { addProduct } from "../store/reducers/cart";
import productDetailService from "./../services/productDetailService";
const ProductDetail = () => {
  const dispatch = useDispatch();
  const onAddToCart = (product) => {
    dispatch(addProduct(product));
    toast.success("Bn  thm sn phm vo gi hng");
  };
  const { id } = useParams();
  const navigate = useNavigate();
  const [product, setProduct] = useState({
    ProDe_ID: 0,
    proName: "",
    proPrice: "",
    proBrand: "",
    proDescription: "",
    proIngredient: "",
    proVolume: 0,
    proUnit: "",
    proAvatar: "",
    product: {},
  });
  useEffect(() => {
    if (!/\d+/.test(id)) {
      navigate("/not-found");
    }
    productDetailService.get(id).then((res) => {
      if (res.errorCode === 0) {
        setProduct(res.data);
      } else {
        navigate("/not-found");
      }
    });
  }, [id, navigate]);
  // console.log(product[0].img.Img1);
  return (
    <>
      {/* */}

      <div>
        <section className="bg-light">
          <div className="container pb-5">
            <div className="row">
              <div className="col col-lg-5 mt-5">
                <div
                  style={{ color: "#59ab6e !important" }}
                  id="template-mo-zay-hero-carousel"
                  className="carousel slide"
                  data-bs-ride="carousel"
                >
                  <ol className="carousel-indicators">
                    <li
                      style={{ color: "#59ab6e !important" }}
                      data-bs-target="#template-mo-zay-hero-carousel"
                      data-bs-slide-to={0}
                      className="active"
                    />
                    <li
                      data-bs-target="#template-mo-zay-hero-carousel"
                      data-bs-slide-to={1}
                    />
                    <li
                      data-bs-target="#template-mo-zay-hero-carousel"
                      data-bs-slide-to={2}
                    />
                  </ol>
                  <div className="carousel-inner">
                    <div className="carousel-item active">
                      <div className="mx-auto col-auto order-lg-last">
                        <img
                          className="img-fluid"
                          src={product.proAvatar}
                          alt={product.proName}
                        />
                      </div>
                    </div>
                    <div className="carousel-item">
                      <div className="mx-auto col-auto order-lg-last">
                        <img
                          className="img-fluid"
                          src={product.proImg1}
                          alt={product.proName}
                        />
                      </div>
                    </div>
                    <div className="carousel-item">
                      <div className="mx-auto col-auto order-lg-last">
                        <img
                          className="img-fluid"
                          src={product.proImg2}
                          alt={product.proName}
                        />
                      </div>
                    </div>
                  </div>
                  <a
                    style={{ color: "#59ab6e !important" }}
                    className="carousel-controimport { useDispatch } from 'react-redux';
                    carousel-control-prev textimport ProductDetailService from './../services/productDetailService';
-decoration-none w-auto ps-3"
                    href="#template-mo-zay-hero-carousel"
                    role="button"
                    data-bs-slide="prev"
                  >
                    <i className="fas fa-chevron-left" />
                  </a>
                  <a
                    style={{ color: "#59ab6e" }}
                    className="carousel-control-next text-decoration-none w-auto pe-3"
                    href="#template-mo-zay-hero-carousel"
                    role="button"
                    data-bs-slide="next"
                  >
                    <i className="fas fa-chevron-right" />
                  </a>
                </div>
              </div>
              {/* end slide */}
              <div className="col-lg-7 mt-5">
                <div className="card">
                  <div className="card-body">
                    <h1 className="h2">{product.proName}</h1>
                    <p className="h3 py-2 text-danger">
                      <span className="h5 text-dark">Gi: </span>
                      {product.proPrice}
                      <sup>
                        <i className="fa-solid fa-dong-sign"></i>
                      </sup>
                    </p>
                    <ul className="list-inline">
                      <li className="list-inline-item">
                        <h5>Thng Hiu:</h5>
                      </li>
                      <li className="list-inline-item">
                        <p className="text-muted">
                          <strong>{product.proBrand}</strong>
                        </p>
                      </li>
                    </ul>
                    <h5>Miu t sn phm:</h5>
                    <p>{product.proDescription}</p>
                    <ul className="list-inline">
                      <li className="list-inline-item">
                        <h5>Th tch :</h5>
                      </li>
                      <li className="list-inline-item">
                        <p className="text-muted">
                          <strong>
                            {product.proVolume}
                            {product.proUnit}
                          </strong>
                        </p>
                      </li>
                    </ul>
                    {/* <form action method="GET"> */}
                    <form action="trueT">
                      <input
                        type="hidden"
                        name="product-title"
                        defaultValue="Activewear"
                      />
                      <div className="row pb-3">
                        <div className="col d-grid mx-5">
                          <button
                            type="button"
                            className="btn btn-success btn-lg"
                            name="submit"
                            defaultValue="buy"
                            onClick={() => onAddToCart(product)}
                          >
                            Thm Vo Gi Hng
                          </button>
                        </div>
                      </div>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </section>

        <div className="container">
          <div className="row fs-3 fw-bold mt-3 text-success">
            Thnh Phn Chi Tit
          </div>
          <div className="border p-5 mt-4 mb-4">{product.proIngredient}</div>
        </div>
      </div>
    </>
  );
};

export default ProductDetail;
