import React, { useState, useEffect } from "react";
import CardItem from "./../components/CardItem";
import { useDispatch } from "react-redux";
import { addProduct } from "../store/reducers/cart";
import { toast } from "react-toastify";
import productDetailService from "./../services/productDetailService";
import { useNavigate, useParams } from "react-router-dom";
import productService from "./../services/ProductService";

const Product = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const onAddToCart = (product) => {
    dispatch(addProduct(product));
    toast.success("Bn  thm sn phm vo gi hng");
  };

  const [products, setProducts] = useState([]);
  const [productTypes, setProductTypes] = useState([]);
  const [producTyps, setProducTyps] = useState([]);

  const { id } = useParams();

  useEffect(() => {
    if (id) {
      productService.get(id).then((res) => {
        if (res.errorCode === 0) {
          setProductTypes(res.data.product_details);
        } else {
          navigate("/not-found");
        }
      });
    }
    loadData();
  }, [id, navigate]);

  const loadData = () => {
    productDetailService.list().then((res) => setProducts(res.data));
    productService.list().then((res) => setProducTyps(res.data));
  };

  const showDetail = (e, id) => {
    if (e) e.preventDefault();
    navigate(`/productdetail/${id}`);
  };

  const showType = (e, id) => {
    if (e) e.preventDefault();
    navigate(`/product/${id}`);
    window.scrollTo(0, 0);
  };

  return (
    <>
      {/* <!-- Start Content --> */}
      <div>
        <div className="container py-5">
          <div className="row">
            <div className="col">
              <div className="row row-cols-3 row-cols-lg-6">
                {producTyps.map((product, idx) => (
                  <div className="col p-3 mt-3" key={idx}>
                    <a href="/#" onClick={(e) => showType(e, product.Pro_ID)}>
                      <div className="text-center">
                        <img
                          src={`${process.env.PUBLIC_URL}/${product.proImg}.png`}
                          alt={product.proType}
                          style={{ width: "100px" }}
                          className="rounded-circle img-fluid border "
                        />
                      </div>
                    </a>
                    <h2 className="h6 text-center mt-3 mb-3">
                      {product.proType}
                    </h2>
                  </div>
                ))}
              </div>
              <div className="row row-cols-1 row-cols-lg-4 row-cols-md-2 gy-3">
                {id ? (
                  <>
                    {productTypes.map((product, idx) => (
                      <CardItem
                        key={idx}
                        product={product}
                        showProductDetail={(e) =>
                          showDetail(e, product.ProDe_ID)
                        }
                        addToCart={onAddToCart}
                      />
                    ))}
                  </>
                ) : (
                  <>
                    {products.map((product, idx) => (
                      <CardItem
                        key={idx}
                        product={product}
                        showProductDetail={(e) =>
                          showDetail(e, product.ProDe_ID)
                        }
                        addToCart={onAddToCart}
                      />
                    ))}
                  </>
                )}
              </div>
            </div>
          </div>
        </div>
        {/* End Content */}
        {/* Start Brands */}
      </div>
      {/* <!--End Brands--> */}
    </>
  );
};

export default Product;
