import React, { useState, useEffect } from "react";
import Image from "./Image";
const CartItem = (props) => {
  const { cart, deleteToCart, increase, decrease } = props;

  return (
    <>
      <div className="container pt-3 mt-3">
        <div className="border border-success rounded-3 p-3">
          <div className="row gy-4">
            <Image
              link={cart.proAvatar}
              colSize="12 col-lg-4"
              alt={cart.proName}
            />
            <div className="col-12 col-lg-8">
              <div className="row gy-2">
                <div className="col-9 col-md-10">{cart.proName}</div>
                <div className="col-3 col-md-2">
                  <button
                    className="rounded-3 outline-success "
                    onClick={() => deleteToCart(cart.ProDe_ID)}
                  >
                    <i className="bi-trash h2  text-success"></i>
                  </button>
                </div>

                <div className="col-12 text-start">
                  <ul className="list-inline">
                    <li
                      className="list-inline-item mb-2 me-4"
                      onClick={() => decrease(cart.ProDe_ID)}
                    >
                      <span
                        className="btn btn-success ps-4 pe-4"
                        id="btn-minus"
                      >
                        -
                      </span>
                    </li>
                    <li className="list-inline-item mb-2 me-4">
                      <span
                        className="badge bg-secondary ps-4 pe-4 pt-2 pb-2"
                        // id="var-value"
                      >
                        {cart.quantity}
                      </span>
                    </li>
                    <li
                      className="list-inline-item"
                      onClick={() => increase(cart.ProDe_ID)}
                    >
                      <span className="btn btn-success ps-4 pe-4" id="btn-plus">
                        +
                      </span>
                    </li>
                  </ul>
                </div>
                <div className="col-12 text-start mt-0">
                  <h3 className="text-danger">
                    {cart.proPrice}
                    <sup>
                      <i className="fa-solid fa-dong-sign"></i>
                    </sup>
                  </h3>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default CartItem;
