import { createSlice } from "@reduxjs/toolkit";

export const cartSlice = createSlice({
  name: "cart",
  initialState: {
    quantity: 0,
    carts: [],
    totalCost: 0,
  },
  reducers: {
    addProduct: (state, action) => {
      if (state.quantity === 0) {
        const {
          ProDe_ID,
          quantity = 1,
          proName,
          proAvatar,
          proPrice,
        } = action.payload;
        state.carts.push({ ProDe_ID, quantity, proName, proAvatar, proPrice });
        state.totalCost += quantity * proPrice;
      } else {
        let check = false;
        state.carts.forEach((item, idx) => {
          if (item.ProDe_ID === action.payload.ProDe_ID) {
            state.carts[idx].quantity++;
            check = true;
            state.totalCost += state.carts[idx].proPrice;
            console.log(item.ProDe_ID);
          }
        });
        if (!check) {
          const {
            ProDe_ID,
            quantity = 1,
            proName,
            proAvatar,
            proPrice,
          } = action.payload;
          state.carts.push({
            ProDe_ID,
            quantity,
            proName,
            proAvatar,
            proPrice,
          });
          state.totalCost += quantity * proPrice;
        }
      }
      state.quantity++;
    },
    removeProduct: (state, action) => {
      const cart = state.carts.find((cart) => cart.ProDe_ID === action.payload);
      console.log(cart);
      if (cart) {
        let price = cart.proPrice;
        let quantity = cart.quantity;
        state.totalCost -= price * quantity;
        state.quantity -= quantity;
        state.carts.splice(state.carts.indexOf(cart), 1);
      }
    },
    increase: (state, action) => {
      const cart = state.carts.find((cart) => cart.ProDe_ID === action.payload);
      if (cart) {
        cart.quantity++;
        state.totalCost += cart.proPrice;
        state.quantity++;
      }
    },
    decrease: (state, action) => {
      const cart = state.carts.find((cart) => cart.ProDe_ID === action.payload);
      if (cart) {
        if (cart.quantity > 0) {
          cart.quantity--;
          state.totalCost -= cart.proPrice;
          state.quantity--;
        }
        if (cart.quantity === 0) {
          state.carts.splice(state.carts.indexOf(cart), 1);
        }
      }
    },
    close: (state) => {
      state.totalCost = 0;
      state.quantity = 0;
      state.carts = [];
    },
  },
});

export const { addProduct, removeProduct, increase, decrease, close } =
  cartSlice.actions;
export default cartSlice.reducer;
