import api from "./api";
const list = () => api.get(api.url.productsDetail);
const get = (id) => api.get(`${api.url.productsDetail}/${id}`);
const add = (data) => api.post(api.url.productsDetail, data);
const update = (id, data) => api.put(`${api.url.productsDetail}/${id}`, data);
const remove = (id) => api.delete(`${api.url.productsDetail}/${id}`);

const productDetailService = {
  list,
  get,
  add,
  update,
  remove,
};
export default productDetailService;
