import React from "react";

const TableRow = (props) => {
  const { name, quantity, stt, price } = props;

  return (
    <>
      <tr className="table-success">
        <th scope="row" key={stt}>
          {stt}
        </th>
        <td>{name}</td>
        <td>{quantity}</td>
        <td>{price}</td>
      </tr>
    </>
  );
};

export default TableRow;
