import React, { useState, useEffect } from "react";
import { Link, useNavigate, useParams } from "react-router-dom";
import bannerService from "./../services/bannerService";

const Blog = () => {
  const { id } = useParams();
  const navigate = useNavigate();
  const [banner, setBanner] = useState({
    Ban_ID: 0,
    banName: "",
    banPlace: "",
    banDes: "",
    banTime: "",
    banImg: "",
  });
  useEffect(() => {
    if (!/\d+/.test(id)) {
      navigate("/not-found");
    }
    bannerService.get(id).then((res) => {
      if (res.errorCode === 0) {
        setBanner(res.data);
      } else {
        navigate("/not-found");
      }
    });
  }, [id, navigate]);
  return (
    <>
      <div className="container">
        <h1 className="text-start text-success text-opacity-75 mb-3 mt-5">
          {banner.banName}
        </h1>
        <div className="row">
          <div className="container">
            <div className="col text-center">
              {" "}
              <Link to="/#" className="">
                <img
                  className="img-fluid"
                  src={`${process.env.PUBLIC_URL}/${banner.banImg}`}
                  alt={banner.banName}
                  style={{ width: "80rem" }}
                />
              </Link>
              <p className=" fs-4 mb-2 text-start mt-3">{banner.banDes}</p>
              <ul>
                <li>
                  <p className="fs-4 mb-2 text-start">
                    Thi gian p dng: {banner.banTime}
                  </p>
                </li>
                <li>
                  <p className="fs-4 mb-2 text-start">
                    a im: {banner.banPlace}
                  </p>
                </li>

                <li>
                  <p className="fs-4 mb-2 text-start">
                    Hotline: 0938449788  0912222592
                  </p>
                </li>
                <li>
                  <p className="fs-4 mb-2 text-start">
                    Website: www.yingbeauty.com
                  </p>
                </li>
                <li>
                  <p className="fs-4 mb-2 text-start">
                    Fanpage: Ying Skin Beauty
                  </p>
                </li>
              </ul>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Blog;
