import React, { useState, useEffect } from "react";
import CardItem from "../components/CardItem";
import { useDispatch } from "react-redux";
import { addProduct } from "../store/reducers/cart";
import { toast } from "react-toastify";
import productDetailService from "./../services/productDetailService";
import { useNavigate } from "react-router-dom";
import productService from "./../services/ProductService";
import bannerService from "./../services/bannerService";
const Home = () => {
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const onAddToCart = (product) => {
    dispatch(addProduct(product));
    toast.success("Bn  thm sn phm vo gi hng");
  };
  const [products, setProducts] = useState([]);
  const [producTypes, setProducTypes] = useState([]);
  const [banners, setBanners] = useState([]);
  useEffect(() => {
    loadData();
  }, []);

  const loadData = () => {
    productDetailService.list().then((res) => setProducts(res.data));
    productService.list().then((res) => setProducTypes(res.data));
    bannerService.list().then((res) => setBanners(res.data));
  };

  const showDetail = (e, id) => {
    if (e) e.preventDefault();
    navigate(`/productdetail/${id}`);
    window.scrollTo(0, 0);
  };
  const showType = (e, id) => {
    if (e) e.preventDefault();
    navigate(`/product/${id}`);
    window.scrollTo(0, 0);
  };

  return (
    <>
      <div>
        {/* Start Banner Hero */}
        <div
          id="template-mo-zay-hero-carousel"
          className="carousel slide"
          data-bs-ride="carousel"
        >
          <ol className="carousel-indicators">
            {banners.map((banner, idx) => (
              <li
                data-bs-target="#template-mo-zay-hero-carousel"
                data-bs-slide-to={idx}
                key={banner.Ban_ID}
                className={idx === 0 ? "active" : ""}
              />
            ))}
          </ol>
          <div className="carousel-inner">
            {banners.map((banner, idx) => (
              <div
                className={idx === 0 ? "carousel-item active" : "carousel-item"}
                key={idx}
              >
                <img
                  className="img-fluid"
                  src={banner.banImg}
                  alt={banner.banName}
                />
              </div>
            ))}
          </div>
          <a
            className="carousel-control-prev text-decoration-none w-auto ps-3"
            href="#template-mo-zay-hero-carousel"
            role="button"
            data-bs-slide="prev"
          >
            <i className="fas fa-chevron-left" />
          </a>
          <a
            className="carousel-control-next text-decoration-none w-auto pe-3"
            href="#template-mo-zay-hero-carousel"
            role="button"
            data-bs-slide="next"
          >
            <i className="fas fa-chevron-right" />
          </a>
        </div>
        {/* End Banner Hero */}
        {/* Start Categories  */}
        <section className="container">
          <div className="h1 text-success text-center pt-4 fw-blod">
            {" "}
            Danh mc sn phm
          </div>
          <div className="row row-cols-3 row-cols-lg-6">
            {producTypes.map((product, idx) => (
              <div className="col p-3 mt-3" key={idx}>
                <a href="/#" onClick={(e) => showType(e, product.Pro_ID)}>
                  <div className="text-center">
                    <img
                      src={`${process.env.PUBLIC_URL}/${product.proImg}.png`}
                      alt={product.proType}
                      style={{ width: "100px" }}
                      className="rounded-circle img-fluid border "
                    />
                  </div>
                </a>
                <h2 className="h6 text-center mt-3 mb-3">{product.proType}</h2>
              </div>
            ))}
          </div>
        </section>
        {/* End Categories */}
        {/* Start Best Seller  */}
        <section className="bg-light">
          <div className="container py-3">
            <div className="row text-center py-3">
              <div className="col-lg-6 m-auto">
                <h1 className="h1 text-success fw-blod">Hng mi v</h1>
              </div>
            </div>
            <div className="row row-cols-1 row-cols-md-2 row-cols-xl-4 gy-3">
              {products.map((product, idx) => (
                <CardItem
                  key={idx}
                  product={product}
                  showProductDetail={(e) => showDetail(e, product.ProDe_ID)}
                  addToCart={onAddToCart}
                />
              ))}
            </div>
          </div>
        </section>
        {/* End Best Seller */}
      </div>
    </>
  );
};

export default Home;
