import React, { useState, useEffect } from "react";
import productService from "./../services/ProductService";
import { Link, useNavigate } from "react-router-dom";
const Footer = () => {
  const [producTypes, setProducTypes] = useState([]);
  const navigate = useNavigate();
  useEffect(() => {
    loadData();
  }, []);
  const loadData = () => {
    productService.list().then((res) => setProducTypes(res.data));
  };
  console.log(producTypes);
  const showType = (e, id) => {
    if (e) e.preventDefault();
    navigate(`/product/${id}`);
    window.scrollTo(0, 0);
  };
  return (
    <>
      <footer className="bg-dark" id="tempaltemo_footer">
        <div className="container">
          <div className="row">
            <div className="col-md-4 pt-5">
              <h2 className="h2 text-success border-bottom pb-3 border-light logo">
                Ying Shop
              </h2>
              <ul className="list-unstyled text-light footer-link-list">
                <li>
                  <i className="fas fa-map-marker-alt fa-fw" />
                  Saigon Tech Building Quang Trung
                </li>
                <li>
                  <i className="fa fa-phone fa-fw" />
                  <a className="text-decoration-none" href="tel:010-020-0340">
                    010-020-0340
                  </a>
                </li>
                <li>
                  <i className="fa fa-envelope fa-fw" />
                  <a
                    className="text-decoration-none"
                    href="mailto:info@company.com"
                  >
                    yingshop@gmail.com
                  </a>
                </li>
              </ul>
              <div className="col-auto me-auto">
                <ul className="list-inline text-left footer-icons">
                  <li className="list-inline-item border border-light rounded-circle text-center">
                    <a
                      className="text-light text-decoration-none"
                      target="_blank"
                      href="/#"
                    >
                      <i className="fab fa-facebook-f fa-lg fa-fw" />
                    </a>
                  </li>
                  <li className="list-inline-item border border-light rounded-circle text-center">
                    <a
                      className="text-light text-decoration-none"
                      target="_blank"
                      href="/#"
                    >
                      <i className="fab fa-instagram fa-lg fa-fw" />
                    </a>
                  </li>
                  <li className="list-inline-item border border-light rounded-circle text-center">
                    <a
                      className="text-light text-decoration-none"
                      target="_blank"
                      href="/#"
                    >
                      <i className="fab fa-twitter fa-lg fa-fw" />
                    </a>
                  </li>
                  <li className="list-inline-item border border-light rounded-circle text-center">
                    <a
                      className="text-light text-decoration-none"
                      target="_blank"
                      href="/#"
                    >
                      <i className="fab fa-linkedin fa-lg fa-fw" />
                    </a>
                  </li>
                </ul>
              </div>
            </div>

            <div className="col-md-4 pt-5">
              <h2 className="h2 text-light border-bottom pb-3 border-light">
                Sn Phm
              </h2>
              <ul className="list-unstyled text-light footer-link-list">
                {producTypes.map((type, idx) => (
                  <li key={idx}>
                    <a
                      className="text-decoration-none"
                      href="/#"
                      onClick={(e) => showType(e, type.Pro_ID)}
                    >
                      {type.proType}
                    </a>
                  </li>
                ))}
              </ul>
            </div>

            <div className="col-md-4 pt-5">
              <h2 className="h2 text-light mt-2 border-bottom pb-3 border-light">
                u i
              </h2>
              <Link className="text-decoration-none" to="/promotion">
                u i hin ti
              </Link>
              <img
                src="https://www.watsons.vn/medias/27.02.
                  19-so-cong-thuong?context=bWFzdGVyfHJvb3R8ODEwMHxpbWFnZS9wbmd8aDAwL2g2Yy85MTQ0MDcwNzMzODU0LnBuZ3w0ODgwYTRhYTE1OWFlMjllMjViOTk3MWU1Zjk3NTJhZjM2YjlmNWNmZjJlYzllNzNlZWI5MDVjYjIzOGNiNTEz"
                alt="bocongthuong"
                className="img-fluid mt-5"
              />
            </div>
          </div>

          <div className="row text-light ">
            <div className="col mb-3">
              <div className="w-100 my-3 border-top border-light">
                <p className="text-end"> &copy; all right reserved, 2022</p>
              </div>
            </div>
          </div>
        </div>
      </footer>
    </>
  );
};

export default Footer;
