import api from "./api";
const list = () => api.get(api.url.banners);
const get = (id) => api.get(`${api.url.banners}/${id}`);
const add = (data) => api.post(api.url.banners, data);
const update = (id, data) => api.put(`${api.url.banners}/${id}`, data);
const remove = (id) => api.delete(`${api.url.banners}/${id}`);

const bannerService = {
  list,
  get,
  add,
  update,
  remove,
};
export default bannerService;
