import React, { useState, useEffect } from "react";
import TableRow from "./../components/TableRow";
import { useNavigate, useParams } from "react-router-dom";
import orderService from "./../services/orderService";
import orderDetailService from "./../services/orderDetailService";
import { toast } from "react-toastify";
import { useSelector, useDispatch } from "react-redux";
import { close } from "../store/reducers/cart";
import { ErrorMessage } from "formik";
const OrderDetail = () => {
  const navigate = useNavigate();
  const handleBack = () => {
    navigate("/product");
  };
  const cartsList = useSelector((state) => state.cart.carts);
  const total = useSelector((state) => state.cart.totalCost);
  const customerStr = localStorage.getItem("customer");
  const customerJs = JSON.parse(customerStr);
  const customer = customerJs.customer;
  console.log(customer);
  // console.log(cartsList);
  console.log(customer.phone);

  const dispatch = useDispatch();
  // const { id } = useParams();
  const [order, setOrder] = useState({
    customer: {},
    details: [],
  });
  const [details, setDetails] = useState([]);
  // useEffect(() => {
  //   if (!/\d+/.test(id)) {
  //     navigate("/not-found");
  //   }
  //   orderService.get(id).then((res) => {
  //     if (res.errorCode === 0) {
  //       setOrder(res.data);
  //     } else {
  //       navigate("/not-found");
  //     }
  //   });
  // }, [id, navigate]);

  const addOrderDetail = () => {
    toast.success("Cm n qu khch  mua hng");
    cartsList.forEach((cart) => {
      const price = cart.proPrice;
      const quantity = cart.quantity;
      const prodeID = cart.ProDe_ID;
      const orderDetail = {
        ProDe_ID: prodeID,
        orDePrice: price,
        orDeQuantity: quantity,
      };
      setDetails(details.push(orderDetail));
    });
    console.log(details);
    setOrder(customer, details);
    console.log(order);
    const data = { customer: customer, details: details };
    orderService.add(data).then((res) => {
      if (res.errorCode === 0) {
        dispatch(close());
        // navigate("/product");
      } else {
        console.log(res.message);
      }
    });
  };

  return (
    <>
      <div className="container mt-3 pe-4 ps-4 ">
        <h1 className="text-center pb-3 text-success"> Thng tin n hng</h1>
        <div className="card border-primary bt-5 mb-5">
          <div className="card-header">
            <div className="row">
              <div className="col-5 mt-1 col-lg-2 fw-bold">
                <p>Khch hng:</p>
                <p>a ch: </p>
                <p>in thoi:</p>
                <p>Email:</p>
                {/* <p>t ngy: </p> */}
              </div>
              <div className="col-7 mt-1 py-0 col-lg-7">
                <p>
                  {customer.lastName} {customer.firstName}
                </p>
                <p>{customer.address}</p>
                <p>{customer.phone}</p>
                <p>{customer.email}</p>
                {/* <p>{order[0].date} </p> */}
              </div>
              <div className="col py-3 text-center col-lg-3">
                <div className="row">
                  <div className="col">
                    <p className=" ms-0 text-start fw-bold">M n hng:</p>
                  </div>
                  <div className="col text-start">
                    <p>{customer.id}</p>
                  </div>
                </div>
              </div>
            </div>
          </div>
          <div className="row pt-2 ps-3">
            <p className="h4 text-success pt-1 pb-1 fw-bold">
              Danh sch t hng
            </p>
          </div>

          {/* table */}
          <div className="container">
            <table className="table table-striped table-bordered table-hover">
              <thead>
                <tr>
                  <th scope="col">STT</th>
                  <th scope="col">Tn Sn Phm</th>
                  <th scope="col">S Lng</th>
                  <th scope="col">Thnh Tin</th>
                </tr>
              </thead>
              <tbody>
                {cartsList.map((e, idx) => (
                  <TableRow
                    key={idx}
                    stt={idx + 1}
                    name={e.proName}
                    quantity={e.quantity}
                    price={e.proPrice}
                  />
                ))}
                <tr>
                  <th scope="row" />
                  <td />
                  <td className="h5 text-danger">Tng tin: </td>
                  <td className="text-danger">{total}</td>
                </tr>
              </tbody>
            </table>
          </div>

          <div className="row mb-3 py-0">
            <div className="col text-center gy-1 ">
              <button
                onClick={addOrderDetail}
                type="submit"
                className="btn btn-success btn-lg me-3 "
                data-bs-toggle="modal"
                data-bs-target="#showMessage"
              >
                Xc nhn
              </button>
              <button
                type="submit"
                onClick={handleBack}
                className="btn btn-secondary btn-lg"
              >
                Hy b
              </button>
            </div>
          </div>
        </div>
      </div>

      {/* <div
        className="modal fade"
        id="showMessage"
        tabIndex={-1}
        data-bs-backdrop="static"
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog">
          <div className="modal-content">
            <div className="modal-body">Cm n qu khch  mua hng</div>
            <div className="modal-footer">
              <button
                type="button"
                onClick={() => showMessage}
                className="btn btn-success"
              >
                Tip tc mua sm
              </button>
            </div>
          </div>
        </div>
      </div> */}
    </>
  );
};

export default OrderDetail;
