import React, { useState, useEffect } from "react";
const Image = (props) => {
  const { link, colSize, alt, className } = props;
  const colClass = `col-${colSize ? colSize : 3} ${className}`;
  const linkImg = `${link}`;
  return (
    <>
      <div className={colClass}>
        <a href="/#">
          <img className="img-fluid" src={linkImg} alt={alt} />
        </a>
      </div>
    </>
  );
};

export default Image;
