import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import bannerService from "../services/bannerService";

const Promotion = () => {
  const [banners, setBanners] = useState([]);
  useEffect(() => {
    loadData();
  }, []);
  const loadData = () => {
    bannerService.list().then((res) => setBanners(res.data));
  };
  const navigate = useNavigate();
  const showBlogs = (e, id) => {
    if (e) e.preventDefault();
    navigate(`/blog/${id}`);
    window.scrollTo(0, 0);
  };
  return (
    <>
      <div className="container">
        <h1 className="text-start text-success text-opacity-75 mb-3 mt-3">
          Chng Trnh u i
        </h1>
        {banners.map((banner, idx) => (
          <div className="row" key={idx}>
            <div className="container">
              <div className="col border border-3 p-3 rounded-2 text-center mb-4">
                <Link to="/#" onClick={(e) => showBlogs(e, banner.Ban_ID)}>
                  <img
                    className="img-fluid"
                    src={`${process.env.PUBLIC_URL}/${banner.banImg}`}
                    alt={banner.banName}
                    style={{ width: "80rem" }}
                  />
                </Link>
                <Link
                  to="/#"
                  className="text-decoration-none text-success"
                  onClick={(e) => showBlogs(e, banner.Ban_ID)}
                >
                  <p className="card-title fs-3 fw-bold text-start mt-2">
                    {banner.banName}
                  </p>
                </Link>
                <p className="card-text mb-2 text-start">
                  Thi gian p dng: {banner.banTime}
                </p>
              </div>
            </div>
          </div>
        ))}
      </div>
    </>
  );
};

export default Promotion;
