import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { decrease, increase, removeProduct } from "../store/reducers/cart";
import CartItem from "./../components/CartItem";
import { toast } from "react-toastify";

const Header = (props) => {
  const quantity = useSelector((state) => state.cart.quantity);
  const cartList = useSelector((state) => state.cart.carts);
  const totalCost = useSelector((state) => state.cart.totalCost);
  const navigate = useNavigate();
  const dispatch = useDispatch();
  const onDeleteToCart = (id) => {
    dispatch(removeProduct(id));
    toast.success("Bn  xa sn phm ra khi gi hng");
  };

  const [count, setCount] = useState(1);
  const onIncrease = (id) => {
    dispatch(increase(id));
    setCount(count + 1);
  };
  const onDecrease = (id) => {
    dispatch(decrease(id));
    if (count > 1) {
      setCount(count - 1);
    }
  };

  const showOrderForm = () => {
    navigate("/orderform");
  };

  return (
    <>
      <nav className="navbar navbar-expand-lg navbar-light shadow ">
        <div className="container d-flex justify-content-between align-items-center">
          <Link
            className="navbar-brand text-success logo h1 align-self-center"
            to="/"
          >
            Ying Beauty
          </Link>
          <button
            className="navbar-toggler border-0"
            type="button"
            data-bs-toggle="collapse"
            data-bs-target="#templatemo_main_nav"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation"
          >
            <span className="navbar-toggler-icon" />
          </button>
          <div
            className="align-self-center collapse navbar-collapse  justify-content-lg-between"
            id="templatemo_main_nav"
          >
            <div className="flex-fill">
              <ul className="nav navbar-nav  justify-content-between mx-lg-auto">
                <li className="nav-item">
                  <Link className="nav-link" to="/">
                    Trang Ch
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/product">
                    Sn Phm
                  </Link>
                </li>
                <li className="nav-item text-start me-4">
                  <Link className="nav-link" to="/promotion">
                    u i
                  </Link>
                </li>
              </ul>
            </div>
            <div className="navbar align-self-center d-flex ">
              <div className="flex-sm-fill mt-3 mb-4 col-9 col-sm-5 pr-3">
                <div className="input-group ">
                  <input
                    type="text"
                    className="form-control"
                    id="inputMobileSearch"
                    placeholder="Search ..."
                  />
                  <button className="text-success rounded-2 border-0">
                    <i className="fa fa-fw fa-search " />
                  </button>
                </div>
              </div>
              <Link
                className="nav-icon position-relative text-decoration-none ms-3"
                data-bs-toggle="modal"
                data-bs-target="#exampleModal"
                to="/#"
              >
                <i className="fa fa-fw fa-cart-arrow-down text-dark mr-1" />
                <span className="position-absolute  translate-middle badge rounded-pill bg-light text-dark">
                  {quantity}
                </span>
              </Link>
            </div>
          </div>
        </div>
      </nav>
      <div
        className="modal modal-lg fade"
        id="exampleModal"
        tabIndex={-1}
        aria-labelledby="exampleModalLabel"
        aria-hidden="true"
      >
        <div className="modal-dialog modal-dialog-scrollable">
          <div className="modal-content">
            <div className="modal-header">
              <h5 className="modal-title" id="exampleModalLabel">
                Gi hng
              </h5>
              <button
                type="button"
                className="btn-close"
                data-bs-dismiss="modal"
                aria-label="Close"
              />
            </div>
            <div className="modal-body">
              {cartList.map((cart, idx) => (
                <CartItem
                  key={idx}
                  cart={cart}
                  deleteToCart={onDeleteToCart}
                  increase={onIncrease}
                  decrease={onDecrease}
                />
              ))}
              <div className="container">
                {totalCost === 0 ? (
                  <h3 className="text-center text-muted">
                    Cha c sn phm trong gi hng
                  </h3>
                ) : (
                  <h2 className="text-end mt-3">
                    Total Cost :{" "}
                    <span className="text-success">{totalCost}</span>
                  </h2>
                )}
              </div>
            </div>
            <div className="modal-footer">
              <button
                type="button"
                className="btn btn-secondary"
                data-bs-dismiss="modal"
              >
                Tr V
              </button>
              <button
                type="button"
                disabled={totalCost === 0}
                onClick={showOrderForm}
                className="btn btn-success"
                data-bs-dismiss="modal"
              >
                Thanh Ton
              </button>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

export default Header;
